class DetailController {
  constructor($http, $stateParams, $interval, myConfig, AppsService) {
  	"ngInject";
    this.name = 'detail';
    this.$http = $http;
    this.service = AppsService;
    this.apiendpoint = myConfig.ApiEndpoint;
    this.urlDownloadConfig = this.apiendpoint + "/v1/org/configfile/"+ $stateParams.id;
    

    let _this = this;
    let compare = [];

    this.getOrgDetail($stateParams.id).then(data => this.appDetail = data);

    this.getDevicesFunc = function(id) {
      this.getDevices(id).then(data => {
        this.deviceList = data;
        for (let item of data) {
          var status = '';
          if (item.isOnline){
            status = "OK";
          } else {
            status = "offline";
          }

          this.runCheckOnline(status, item.Id, item.ipAddress);  
        };
      });
    }
    
    this.getDevicesFunc($stateParams.id);

    let stop;
    this.runUpdate = function () {
      stop = $interval(function () {
        _this.getDevicesFunc($stateParams.id);
      }, 7000);
    };

    this.stopUpdate = function () {
      $interval.cancel(stop);
    };

    this.runUpdate();


    this.supported = false;
    this.textToCopy = 'I can copy by clicking!';

    this.success = function () {
      console.log('Copied!');
    };

    this.fail = function (err) {
      console.error('Error!', err);
    };

    this.runCheckOnline = function(status, deviceId, deviceIpAddress) {
      var orgId = parseInt($stateParams.id);
      var deviceId = deviceId;
      this.checkOnline(deviceIpAddress, _this).then(data => {
        if (status != data) {
          var devicestatus = true;
          if (data == "offline") {
            devicestatus = false;
          }
          _this.updateStatus(orgId, deviceId, devicestatus, _this.appDetail.apiKey);
        }
      });
    };

    //console.log(this.random.getName());
  }

  $onDestroy() {
    this.stopUpdate();
    console.log('detail.controller destroyed');
  }

  getOrgDetail(orgId) {
		return this.$http.get(this.apiendpoint+"/v1/org/detail/" + orgId)
		.then(response => response.data);
	}

  getDevices(orgId) {
    return this.$http.get(this.apiendpoint+"/v1/device/" + orgId)
    .then(response => response.data);
  }

  shortUUID(uuid) {
    return uuid.substring(0, 7);
  }

 /* refreshDevices(_this, orgId) {
    _this.$interval(function() {
      _this.getDevices(orgId);
    }, 5000);
  }*/

  checkOnline(deviceIp, state) {
    var deviceApiEndpoint = 'http://' + deviceIp + ':8080';
    return this.$http.get(deviceApiEndpoint + '/ping', {timeout: 3000})
      .then(function successCallback(response) {
       return "OK";
     }, function errorCallback(response) {
       return 'offline';
       //$('#btnCheck').addClass('disabled');
     })
   };

  updateStatus(orgId, deviceId, online, apikey) {    
    let _this = this;
    var config = {
       headers: {'Content-Type': 'application/x-www-form-urlencoded'}
    }

    var data = {'appId': orgId, 'deviceId':deviceId, 'status': online};
    this.$http.post(this.apiendpoint + '/v1/device/' + orgId + '/updatestatus?apikey='+apikey, data, config)
     .then(function (response) {
       if (response.status == 200) {
          _this.getDevices(orgId).then(data => {
            _this.deviceList = data;
          });
       }
    });
  }


  deleteOrg(orgId, name) {
    this.service.deleteOrg(orgId, name);
  }
}

export default DetailController;



