import angular from 'angular';

class AppsService {
  constructor($http, $location, myConfig) {
    this.$http = $http;
    this.$location = $location;
    this.apiendpoint = myConfig.ApiEndpoint;
  }

  deleteOrg(orgId, name) {
    var r = confirm("Are you sure you want to delete " + name + "?");
    if (r == true) {
      let _this = this;
      this.$http.post(this.apiendpoint+"/v1/org/delete/" + orgId)
      .then(function(response) {
        if (response.status == 200) {
          _this.$location.path("/");
        }
      })
    }
  };
}

AppsService.$inject = ['$http', '$location', 'myConfig'];

export default AppsService;



