import angular from 'angular';
import uiRouter from 'angular-ui-router';
import deviceappComponent from './deviceapp.component';

let deviceappModule = angular.module('deviceapp', [
  uiRouter
])

.config(($stateProvider) => {
    "ngInject";

    $stateProvider
    .state('apps.deviceapps', {
      url: '/app/:id/device/:deviceid',
      component: 'deviceapp'
    });
})

.component('deviceapp', deviceappComponent)

.name;

export default deviceappModule;



