class DeviceappController {
  constructor($http, $stateParams, $interval, $timeout, $q, myConfig, AppsService) {
    "ngInject";
    this.$http = $http;
    let _this = this;
    this.service = AppsService;
    this.apiendpoint = myConfig.ApiEndpoint;
    this.name = 'deviceapp';
    this.changename = '';
    this.currentState = '';

    this.getOrgDetail($stateParams.id).then(data => this.appDetail = data);
    let compare = '';

    let stopLoadpage;

    this.getDeviceDetailFunc = function() {
      this.getDeviceDetail($stateParams.id, $stateParams.deviceid).then(data => {
        if (data.isOnline){
          compare = "OK";
        } else {
          compare = "offline";
        }
        this.deviceDetail = data;
        this.changename = data.name;
        this.getApp(data.uuid).then(data => {
          this.deviceApp = data;
          this.appurl = 'http://' + this.deviceDetail.ipAddress + ":" + data.env;
          this.runCheckOnline(this.deviceDetail.ipAddress);
        });
      });
    }

    this.pageInfoReload = function() {
      stopLoadpage = $interval(function () {
        _this.getDeviceDetailFunc();
      }, 6000);
    }

    this.stoppageInfoReload = function () {
      $interval.cancel(stopLoadpage);
    };
    
    this.pageInfoReload();
    this.getDeviceDetailFunc();
    
    //========================= CHECK ONLINE ========================
    
    //let stopCheck;
    this.runCheckOnline = function(deviceId) {
      this.checkOnline(deviceId, _this).then(data => {
        if (compare != _this.currentState) {
          //_this.getDeviceDetailFunc();
          var devicestatus = true;
          if (_this.currentState == "offline") {
            devicestatus = false;
          }
          var orgId = parseInt($stateParams.id);
          var deviceId = parseInt($stateParams.deviceid);
          _this.updateStatus(orgId, deviceId, devicestatus, _this.appDetail.apiKey);
        } else {
          compare = _this.currentState;
          if (_this.currentState != 'offline') {
            $('#btnCheck').removeClass('disabled');
          }
        }
      });
    };

    this.updateName = function() {
      var config = {
        headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
      }
      var data = {"name":this.changename};
      this.$http.post(this.apiendpoint + "/v1/device/" + $stateParams.id + "/updatename/" + $stateParams.deviceid , data, config)
      .then(function(response) {
        if (response.status == 201) {
        }
      });
    }

    //App detail URL
    this.appurl = '';

    let stop;
    this.runUpdate = function(deviceId) {
      _this.installUpdate(deviceId);
      $('#updateProgress').removeAttr('style');
      $('#updateProgress .progress-bar').addClass('progress-bar-info progress-bar-striped');
      stop = $interval(function () {
        _this.$http.get(_this.apiendpoint + "/v1/device/" + $stateParams.id + "/" + $stateParams.deviceid)
        .then((response) => {
          $('#updateProgress .progress-bar').css('width', response.data.downloadProgress + '%');
          if (response.data.downloadProgress == 100) {
            $interval.cancel(stop);
            $timeout(function() {
              $('#updateProgress').css('display','none');
              $('#updateProgress .progress-bar').css('width', '0%');
              $("#btnInstall").css('display','none');
              $("#btnCheck").removeAttr('style');
              $(".updatedes").removeAttr("style");
              $(".updatedes").html("There are no updates available on this device.");
              $(".updatename").html("Application is up to date.");
            }, 10);
          }
        });
      }, 1000);
    };
    //this.runUpdate();

    let barprogress;
    this.checkingUpdate = function(time) {
      $("#updateProgress").removeAttr("style");
      $(".updatedes").css("display","none");
      $(".updatename").html("Checking for updates...");
      $("#btnCheck").addClass("disabled");
      
      let intervalTime = (time * 1000) / 100;
      let x = 0;
      $('#updateProgress .progress-bar').css('width', '0%');

      barprogress = $interval(function () {
        x = x + 1;
        $('#updateProgress .progress-bar').css('width', x + '%');

        if (x > 100) {
          $interval.cancel(barprogress);
          $timeout(function() {
            $('#updateProgress').css('display','none');
            $('#updateProgress .progress-bar').css('width', '0%');
            $("#btnCheck").removeClass("disabled");
            $(".updatedes").removeAttr("style");
          }, 10);
        }
      }, intervalTime);
    };
    //=================================================

    this.checkUpdate = function(deviceIp) {
    //this.wait(3);
    var dataUpdate;
    var deviceApiEndpoint = 'http://' + deviceIp + ':8080';
    _this.getCheckUpdate(deviceApiEndpoint).then(data => {
      dataUpdate = data;
    });

    let wait = function(time) {
      var deferred = $q.defer();

      $("#updateProgress").removeAttr("style");
      $(".updatedes").css("display","none");
      $(".updatename").html("Checking for updates...");
      $("#btnCheck").addClass("disabled");

      let intervalTime = (time * 1000) / 100;
      let x = 0;
      $('#updateProgress .progress-bar').css('width', '0%');

      barprogress = $interval(function () {
        x = x + 1;
        $('#updateProgress .progress-bar').css('width', x + '%');

        if (x > 100) {
          $interval.cancel(barprogress);
          $timeout(function() {
            $('#updateProgress').css('display','none');
            $('#updateProgress .progress-bar').css('width', '0%');
            $("#btnCheck").removeClass("disabled");
            $(".updatedes").removeAttr("style");
            deferred.resolve('');
          }, 500);
        }
      }, intervalTime);
      return deferred.promise;
    };

    wait(3)
    .then(function() {
      doUpdate(dataUpdate);
    })
    .catch(function(fallback) {
        //$scope.name += fallback.toUpperCase() + '!!';
      });

    var doUpdate = function(data) {
      if (data != null) {
        if (data.update == 'true') {
          $('.updatename').html('Updates are available.');
          $('.updatedes').html('Device has found a new update: <b>' + data.version + "</b>");
          $('#btnInstall').removeAttr('style');
          $('#btnCheck').css('display','none');
        } else {
          $('.updatename').html('Device\'s application is up to date.');
          $('.updatedes').html('No updates are available.');
          $('#btnCheck').removeAttr('style');
          $('#btnInstall').css('display','none');
        }
      } else {
        $timeout(function() {
          doUpdate(data);
        }, 1000);
      };
    };
  };
}

  //================================ EVENT FUNCTION ===========================//
  $onDestroy() {
    this.stoppageInfoReload();
    //console.log('detail.controller destroyed');
  }

  shortString(str, number) {    
    return str ? str.substring(0, number) : '';
  };

  getOrgDetail(orgId) {
    return this.$http.get(this.apiendpoint+"/v1/org/detail/" + orgId)
    .then(response => response.data);
  };

   getDeviceDetail(orgId, deviceId) {
     return this.$http.get(this.apiendpoint+"/v1/device/" + orgId + "/" + deviceId)
     .then(response => response.data);
   };

   getApp(deviceId) {
     return this.$http.get(this.apiendpoint+"/v1/app/" + deviceId)
     .then(response => response.data);
   };

   getCheckUpdate(deviceApiEndpoint) {
     return this.$http.get(deviceApiEndpoint + '/v1/checkupdate')
     .then(response => response.data);
   }

   installUpdate(deviceIp) {
     var deviceApiEndpoint = 'http://' + deviceIp + ':8080';
     this.$http.post(deviceApiEndpoint + '/v1/installupdate')
     .then(response => response.data);
   };

   updateProgressBar(percent) {
     $("#updateProgress .progress-bar").css("width",percent+'%').attr('aria-valuenow', percent);
   };

   checkOnline(deviceIp, state) {
     var deviceApiEndpoint = 'http://' + deviceIp + ':8080';
     return this.$http.get(deviceApiEndpoint + '/ping', {timeout: 3000})
     .then(function successCallback(response) {
       state.currentState = "OK";
     }, function errorCallback(response) {
       state.currentState = 'offline';
       $('#btnCheck').addClass('disabled');
     })
   };

   updateStatus(orgId, deviceId, online, apikey) {    
      let _this = this;
      var config = {
         headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }

      var data = {'appId': orgId, 'deviceId':deviceId, 'status': online};
      this.$http.post(this.apiendpoint + '/v1/device/' + orgId + '/updatestatus?apikey='+apikey, data, config)
       .then(function (response) {
         if (response.status == 200) {
            _this.getDeviceDetail(orgId, deviceId).then(data => _this.deviceDetail = data);
         }
      });
   }

   deleteOrg(orgId, name) {
      this.service.deleteOrg(orgId, name);
   }
}

export default DeviceappController;



