class ListController {
  constructor($http, myConfig) {
    // affect all parameters to this
    "ngInject";
    this.name = 'home';
    this.$http = $http;

    this.apiendpoint = myConfig.ApiEndpoint;

    this.setActive = function(index) {
      this.activeIndex = index;
    };

    this.initOrgs = function() {
      this.getOrgs().then(data => this.testList = data);
    }

    this.initOrgs();

    this.deviceOptions = [{"name": "Raspberry Pi 2", "value": "Raspberry Pi 2"}, {
      "name": "Raspberry Pi 3",
      "value": "Raspberry Pi 3"
    }];
    this.deviceOption = this.deviceOptions[0];

    this.selectAction = function () {
      console.log(this.deviceOption.value);
    };
  }

  getOrgs() {
    return this.$http.get(this.apiendpoint + "/v1/org")
      .then(response => response.data);
  }

  createOrg() {
    let _this = this;
    var config = {
      headers: {'Content-Type': 'application/x-www-form-urlencoded'}
    }

    var data = {"name": "" + this.appname + "", "deviceType": "" + this.deviceOption.value + "", "repository": "none"};
    this.$http.post(this.apiendpoint + "/v1/org", data, config)
      .then(function (response) {
        if (response.status == 201) {
          _this.initOrgs();
        }
      });
  }

  getDeviceCount(orgId) {
    return this.$http.get(this.apiendpoint+"/v1/device/" + orgId)
      .then(response => response.data.length);
  }

}

export default ListController;



